@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--header-color);
}

.nav {
  max-width: 60.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: calc(var(--header-height) + 1.5rem);
  column-gap: 1rem;
}
.nav__list {
  display: flex !important;
  column-gap: 2rem;
}
.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--m-font-size);
  font-weight: var(--font-medium);
}
.nav__link:hover {
  color: var(--first-color);
}
.nav__icon {
  font-size: 1.2rem;
}
.nav__logo {
  color: var(--title-color);
  font-weight: var(--font-medium);
}
.nav__logo:hover {
  color: var(--first-color);
}
.nav__btns {
  display: flex;
  align-items: center;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font: inherit;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0 0 var(--header-height 0);
  font-family: var(--body-font);
  font-size: var(--m-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  min-height: 100vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.section {
  padding: 2rem 0 4rem;
  height: 100vh;
}

.section__title {
  font-size: var(--h1-font-size);
}

.section__subtitle {
  display: block;
  font-size: var(--small-font-size);
  margin-bottom: var(--mb-3);
}

.section__title,
.section__subtitle {
  text-align: center;
}

:root {
  --hue-color: 215;
  --first-color-palette: hsl(215, 69%, 61%);
  --first-color-second-palette: hsl(215, 69%, 61%);
  --first-color-second-darken-palette: hsl(216, 68%, 24%);
  --first-color-alt-palette: hsl(215, 57%, 53%);
  --first-color-lighter-palette: hsl(215, 92%, 85%);
  --title-color-palette: hsl(215, 8%, 15%);
  --text-color-palette: hsl(215, 8%, 45%);
  --text-color-light-palette: hsl(215, 8%, 65%);
  --input-color-palette: hsl(215, 70%, 96%);
  --input-color-darken-palette: hsl(215, 79%, 85%);
  --scroll-bar-color-pallete: hsl(0, 91%, 23%);
  --scroll-thumb-color-pallete: hsl(0, 22%, 35%);
  --body-color-palette: hsl(215, 60%, 99%);
  --container-color-palette: #fff;
  --first-color: hsl(var(--hue-color), 69%, 61%);
  --first-color-second: hsl(var(--hue-color), 69%, 61%);
  --first-color-second-darken: hsl(var(--hue-color), 68%, 24%);
  --first-color-alt: hsl(var(--hue-color), 57%, 53%);
  --first-color-lighter: hsl(var(--hue-color), 92%, 85%);
  --title-color: hsl(var(--hue-color), 8%, 15%);
  --text-color: hsl(var(--hue-color), 8%, 45%);
  --text-color-light: hsl(var(--hue-color), 8%, 65%);
  --input-color: hsl(var(--hue-color), 70%, 96%);
  --input-color-darken: hsl(var(--hue-color), 70%, 90%);
  --body-color: hsl(var(--hue-color), 10%, 98%);
  --header-color: hsl(var(--hue-color), 67%, 95%);
  --container-color: #fff;
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

body.dark-theme {
  --first-color-second: hsl(var(--hue-color), 30%, 8%);
  --title-color: hsl(var(--hue-color), 8%, 95%);
  --text-color: hsl(var(--hue-color), 8%, 75%);
  --input-color: hsl(var(--hue-color), 29%, 16%);
  --body-color: hsl(var(--hue-color), 28%, 12%);
  --header-color: hsl(var(--hue-color), 67%, 7%);
  --container-color: hsl(var(--hue-color), 29%, 16%);
  --scroll-bar-color: hsl(var(--hue-color), 12%, 90%);
  --scroll-thumb-color: hsl(var(--hue-color), 37%, 31%);
}

:root {
  --body-font: "Poppins", sans-serif;
}

.container {
  padding: 0;
  max-width: 68.75rem;
  margin: 0 auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}

:root {
  --header-height: 3rem;
  --xs-font-size: 0.75rem;
  --s-font-size: 0.813rem;
  --m-font-size: 0.938rem;
  --l-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;
  --z-tolltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
@media (min-width: 71.875em) {
  :root {
    --xs-font-size: 0.813rem;
    --s-font-size: 0.875rem;
    --m-font-size: 1rem;
    --l-font-size: 3rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--title-color);
  font-weight: var(--font-semi-bold);
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: inherit;
}

ul,
li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  box-shadow: 0;
  border: 0;
  font-family: inherit;
  /* 1 */
  font-size: 100%;
  /* 1 */
  line-height: 1;
  /* 1 */
  margin: 0;
  /* 2 */
}

textarea {
  resize: none;
}